<template>
  <div id="app">
    <div class="movedBlock">
      <p>Destiny has moved to Itch.io! Please click the link below if you are not redirected shortly:</p>
      <a href="https://goblinwife.itch.io/destiny">https://goblinwife.itch.io/destiny</a>
    </div>

  </div>
</template>

<script>
import store, { EventBus } from "./store";

export default {
  name: "App",
  computed: {
    isChoosePage() {
      return store.getters.getCurrentPage === "CHOOSE";
    }
  },
  mounted() {
    EventBus.$on("introOKClicked", () => {
      if (!store.getters.getHasSeenCharIntro) {
        this.$refs.audio.volume = 0.5;
        this.$refs.audio.play();
        store.commit("seenCharIntro");
      }
    });
  }
};
</script>
